<template>
  <v-container>
    <h3>View Employee</h3>
    <Employee :employee="employee" @delete-employee="deleteEmployee" />
  </v-container>
</template>

<script>
import db from "@/components/firebaseinit.js";
import Employee from "../components/Employee";
export default {
  name: "ViewEmployee",
  components: {
    Employee
  },
  data() {
    return {
      employee: null
    };
  },

  mounted() {
    db.collection("employees")
      .where("employee_id", "==", this.$route.params.employee_id)
      .get()
      .then(querySnapshot =>
        querySnapshot.forEach(doc => {
          this.employee = doc.data();
        })
      );
  },
  methods: {
    deleteEmployee() {
      if (confirm(`Are you sure you want to delete ${this.employee.name}?`)) {
        db.collection("employees")
          .where("employee_id", "==", this.employee.employee_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete();
              this.$router.push({
                name: "dashboard"
              });
            });
          });
      }
    }
  }
};
</script>

<style scoped></style>
