<template>
  <v-container>
    <v-card>
      <v-card-title>Informations about: {{ employee.name }}</v-card-title>
      <v-card-text>
        <p>
          Employee ID: <strong>{{ employee.employee_id }}</strong>
        </p>
        <p>
          Position: <strong>{{ employee.position }}</strong>
        </p>
        <p>
          Department:<strong>{{ employee.dept }}</strong>
        </p>
      </v-card-text>
    </v-card>
    <v-btn color="gray" class="mx-4 mt-4" to="/" title="Back"><v-icon>mdi-arrow-left</v-icon></v-btn>
    <v-btn color="red" class="mx-4 mt-4" @click="deleteEmployee" title="Delete"><v-icon>mdi-delete</v-icon></v-btn>
    <v-btn
      class="mt-4"
      color="blue"
      :to="{ name: 'edit-employee', params: { id: employee.employee_id } }"
      title="Edit"
      ><v-icon>mdi-pencil</v-icon></v-btn
    >
  </v-container>
</template>

<script>
export default {
  name: "Employee",
  props: {
    employee: {
      type: Object,
      required: true
    }
  },
  methods: {
    deleteEmployee() {
      this.$emit("delete-employee");
    }
  }
};
</script>

